nav {
  /*
  <nav>
    <ul>
      <li>
        <a>
          <img>
          <div class="channel-name"> <span> </span> </div>
        </a>
      </li>
    </ul>
  </nav>
  */
  height: 100vh;
  width: 72px;
  padding-inline: 12px;
  position: fixed;
  top: 0;
  left: 0;
  background-color: var(--channel-nav-bg);
}
nav ul li {
  width: 48px;
  height: 48px;
  list-style: none;
  margin-bottom: 10px;
  position: relative;
}
nav ul li:first-child {
  margin-block: 20px;
}
nav ul li img {
  border-radius: 50%;
  transition: border-radius 180ms ease-in-out;
}
nav ul li img:hover {
  border-radius: 30%;
}
nav ul li a {
  position: relative;
}
nav ul li a .channel-name {
  position: relative;
  transform: translate(150%, -200%);
  visibility: hidden;
  transition: opacity 200ms ease-in-out;
  opacity: 0;
}
nav ul li a .channel-name span {
  border-radius: 5px;
  padding: 10px;
  background-color: var(--icons-content-sidebar);
  color: white;
  text-align: center;
}
nav ul li a .channel-name::before {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  left: -15px;
  border: solid 8px transparent;
  border-right-color: var(--icons-content-sidebar);
}
nav ul li a:hover .channel-name {
  visibility: visible;
  opacity: 1;
}
nav ul li::after {
  content: "";
  position: absolute;
  width: 8px;
  height: 8px;
  background-color: white;
  border-radius: 5px;
  top: 50%;
  left: -16px;
  transform: translateY(-50%);
  transition: height 180ms ease-in-out;
}
nav ul li:hover::after {
  height: 18px;
}

