aside {
  background-color: var(--sidebar-bg);
  height: 100vh;
  width: 230px;
  position: fixed;
  top: 0;
  left: 72px;
  z-index: 1;
  font-family: "Roboto", sans-serif;
}
aside:hover .courses::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.659);
}
aside .courses {
  scrollbar-width: thin;
  padding-inline: 12px;
  color: white;
  overflow: auto;
  height: 100%;
}
aside .courses::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background-color: rgba(10, 10, 10, 0);
}
aside .courses::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0);
  border-radius: 100vw;
}
aside .courses h2 {
  padding: 5px 5px;
  margin-block: 5px 15px;
  position: relative;
  color: #a5a8ac;
  cursor: pointer;
}
aside .courses h2:hover {
  color: #d3ded5;
}
aside .courses h2::after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%) rotate(310deg);
  left: -10px;
  width: 5px;
  height: 5px;
  border: solid 1px transparent;
  border-bottom-color: #8a8e93;
  border-right-color: #8a8e93;
}
aside .courses h3 {
  font-size: 0.9rem;
  color: #a5a8ac;
  cursor: pointer;
  position: relative;
  padding: 5px 10px;
}
aside .courses h3:hover {
  color: #d3ded5;
}
aside .courses h3::after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%) rotate(310deg);
  left: -10px;
  width: 5px;
  height: 5px;
  border: solid 1px transparent;
  border-bottom-color: #8a8e93;
  border-right-color: #8a8e93;
}
aside .courses a {
  color: #8a8e93;
  display: block;
  padding: 5px 20px;
  border-radius: 5px;
  margin-block: 5px;
  cursor: pointer;
}
aside .courses a:hover {
  color: #d3ded5;
  background-color: #3b3e44;
}

.fold {
  display: none;
}

body {
  background-color: var(--body-bg);
  min-height: 100vh;
}

